/*<!--商品细节图片-->*/
.wrapper {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 648px;
  height: 440px;
  background-color: #fff;
  float: left;
  /*<!--左侧图片区-->*/ }
  .wrapper .gallery {
    width: 540px;
    height: 440px;
    float: left;
    display: table-cell;
    padding-left: 60px;
    margin-top: 60px;
    /*<!--左侧一列图片区-->*/
    /*<!--右侧图片区-->*/ }
    .wrapper .gallery .thumbNail {
      width: 80px;
      height: 440px;
      float: left;
      background-color: #fff; }
      .wrapper .gallery .thumbNail ul {
        position: relative;
        top: 50%;
        transform: translateY(-50%); }
        .wrapper .gallery .thumbNail ul img {
          padding: 10px;
          /*border: 3px solid #ccc;*/
          border: 1px solid rgba(0, 0, 0, 0.06);
