          cursor: pointer;
          margin-top: 10px; }
        .wrapper .gallery .thumbNail ul img:hover {
          box-shadow: inset 0 0 0 2px #B2B2B2; }
        .wrapper .gallery .thumbNail ul .imageShow {
          box-shadow: inset 0 0 0 2px #B2B2B2; }
    .wrapper .gallery .thumb {
      float: left;
      width: 400px;
      height: 400px;
      background-color: #fff;
      overflow: hidden; }
      .wrapper .gallery .thumb img {
        width: 440px;
        height: 440px; }
      .wrapper .gallery .thumb .thumbShow {
        display: block; }

