@charset "UTF-8";
.body {
  width: 1200px;
  margin: 0 auto; }

.panel {
  margin-top: 35px;
  list-style: none;
  border: 1px solid rgba(0, 0, 0, 0.14);
  box-sizing: border-box;
  border-radius: 8px;
  overflow: hidden; }

.panel .title {
  padding-left: 30px;
  font-size: 18px;
  line-height: 60px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.14);
  background: #fafafa;
  color: #666; }

/*carousel*/
.carousel {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 500px;
  overflow: hidden; }

.carouselMask {
  position: absolute;
  z-index: 2;
  width: 100%;
  left: 0px;
  margin: 0;
  height: 500px; }

.carouselMask li {
  position: absolute;
  width: 1200px;
  border-radius: 10px;
  border: 1px solid #ccc;
  overflow: hidden;
  height: 100%;
  box-sizing: border-box;
  opacity: 0;
  transition: opacity 2s ease; }

.carouselMask li a {
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  padding: 0; }

.carouselMask li.active {
  opacity: 1; }

.carousel .carouselToggle {
  position: absolute;
  z-index: 3;
  left: 50%;
  bottom: 15px;
  width: 300px;
  height: 18px;
  margin-left: -150px;
  text-align: center;
  background: transparent; }

.carousel .carouselToggle li {
  position: relative;
  z-index: 3;
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url("/src/images/toggle_dot.png") no-repeat;
  background-size: 100% auto;
  cursor: pointer; }

.carousel .carouselToggle .toggleActive {
  background-position: 0 -18px; }

.smPanel ul li {
  position: relative;
  width: 25%;
  height: 200px;
  float: left;
  box-sizing: border-box;
  border-right: 1px solid rgba(0, 0, 0, 0.14);
  overflow: hidden; }

.smPanel ul li:last-child {
  border: 0px; }

.md_panel {
  margin-top: 30px;
  border: 1px solid rgba(0, 0, 0, 0.14);
  border-bottom: 0px;
  border-radius: 8px;
  overflow: hidden; }

.md_panel ul.cont {
  list-style: none; }

ul.cont li.items {
  position: relative;
  box-sizing: border-box;
  float: left;
  width: 25%;
  height: 429px;
  text-align: center;
  background-color: #fff;
  border-right: 1px solid rgba(0, 0, 0, 0.14);
  border-bottom: 1px solid rgba(0, 0, 0, 0.14); }

ul.cont li.items:last-child {
  border-right: 0; }

.cont .items a.itemLink {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  text-decoration: none;
  transition: all .15s ease-out; }

.cont .items:hover a.itemLink {
  box-shadow: 0 0 38px rgba(0, 0, 0, 0.08) inset; }

.cont .items .imgCover {
  position: relative;
  z-index: 1;
  width: 206px;
  height: 206px;
  display: inline-block;
  margin-top: 50px; }

.cont .items .imgCover img {
  position: absolute;
  z-index: 1;
  left: 0px;
  top: 0px;
  width: 100%; }

.cont .items .imgCover img:first-child {
  z-index: 2; }

.cont .items .infos {
  color: #B2B2B2;
  font-size: 14px; }

.cont .items .infos h6 {
  margin-bottom: 7px;
  color: #424242;
  font-size: 16px;
  font-weight: normal; }

.cont .items .dot {
  margin-top: 15px; }

.cont .items .dot li {
  position: relative;
  z-index: 2;
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  margin: 0 6px;
  cursor: pointer; }

.cont .items .dot li div {
  width: 14px;
  height: 14px;
  border: 1px solid #b2b2b2;
  margin: 0;
  border-radius: 50%; }

.cont .items .dot li span {
  width: 10px;
  height: 10px;
  position: absolute;
  left: 0px;
  right: 0;
  top: 0px;
  bottom: 0;
  margin: auto;
  display: inline-block;
  border-radius: 50%;
  box-sizing: border-box;
  background-position: center;
  background-size: contain;
  overflow: hidden;
  background-image: url("/src/images/dot_fff.jpg"); }

.cont .items .dot li.active div {
  border-width: 3px;
  margin-top: -2px;
  margin-left: -2px; }

.cont .items .dot li.active div span {
  background-image: url("/src/images/dot_000.jpg"); }

.cont .items .price {
  display: block;
  margin-top: 25px;
  width: 100%;
  line-height: 1.2;
  font-size: 18px;
  font-family: Arial;
  color: #DE4037; }

ul.cont li:hover .price {
  display: none; }

.cont .items .operation {
  margin-top: 25px;
  display: none; }

.cont .items .operation a {
  display: inline-block;
  width: 100px;
  height: 30px;
  line-height: 30px;
  background: #e1e1e1;
  color: #5079d9;
  border: 1px solid #aaa;
  border-radius: 4px;
  background-image: linear-gradient(180deg, #fff, #f9f9f9); }

.cont .items .operation a:last-child {
  border: 1px solid #5c81e3;
  background: #678ee7;
  background-image: -webkit-linear-gradient(top, #678ee7, #5078df);
  background-image: linear-gradient(to bottom, #678ee7, #5078df);
  color: #fff; }

ul.cont li:hover .operation {
  display: block; }

/*forum*/
.forum {
  width: 1223px;
  margin: 0 auto;
  margin-top: 50px;
  border: 1px solid #EEEAEA; }

.forum .logo {
  width: 100%;
  height: 50px;
  background-color: #FFFFFF;
  border-radius: 8px;
  overflow: hidden; }

.forum .logo span {
  line-height: 50px;
  font-size: 18px;
  font-family: "黑体";
  color: #666; }

.forum .logo input {
  float: right;
  width: 90px;
  height: 40px;
  margin: 6px 20px;
  background-color: #FFFFFF;
  border: 1px solid #C9C9C9;
  color: #827D7D;
  padding: 0 15 0 19;
  cursor: pointer;
  border-radius: 8px; }

.forum .items {
  display: flex;
  align-items: center; }

.bgPanel ul.cont .firstItem {
  float: left;
  width: 50%;
  overflow: hidden;
  border-right: 1px solid rgba(0, 0, 0, 0.14);
  border-bottom: 1px solid rgba(0, 0, 0, 0.14); }



